<template>
  <div>
    <div class="spotlight-container card">
      <div class="spotlight box-container">
        <div class="content">
          <h1></h1>
          <p>
            <img src="@/assets/images/person-sponsor.jpg" alt="">
          </p>
          <p>
            SITCON 
          </p>
          <p>
            <br />
            <br />
             SITCON<br />
          </p>
          <p><a href="https://sitcon.kktix.cc/events/sitcon2019-individual-sponsor" target="_blank" rel="noopener"></a></p>
          <p> <a href="mailto:contact@sitcon.org" target="_blank" rel="noopener">contact@sitcon.org</a></p>
        </div>
      </div>
      <div
        v-for="(sponsor, index) in sponsors"
        :key="index"
        class="spotlight card"
      >
        <div
          :id="sponsor.slug"
          class="card-container"
        >
          <div class="card-tag">
            <img :src="`/2019/static/img/${sponsor.level}.svg`" alt="">
          </div>
          <div class="logo-image" :style="{ 'background-image': `url(/2019/static/img/sponsor/${sponsor.image})` }">
            <a :href="sponsor.url" target="_blank" rel="external noopener nofollow"></a>
          </div>
          <div class="content">
            <h1>{{ sponsor.name }}</h1>
            <p v-html="sponsor.description.replace(/(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+/gm, `<a href='$&' target='_blank' rel='noopener noreferrer'>$&</a>`)"></p>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import * as Layout from '@/components'
import sponsorData from '../../static/json/sponsor.json'

export default {
  components: {
    ...Layout
  },
  data () {
    return {
      sponsors: sponsorData
    }
  }
}
</script>



